import React from 'react';
import { Bot, User } from 'lucide-react';
import { Message } from '../types/chat';
import DOMPurify from 'dompurify';
import { marked } from 'marked';

interface ChatMessageProps {
  message: Message;
}

function formatTimestamp(date: Date): string {
  // Set timezone to GMT+7
  const options: Intl.DateTimeFormatOptions = {
    hour: '2-digit',
    minute: '2-digit',
    hour12: false,
    timeZone: 'Asia/Bangkok' // GMT+7
  };
  
  const now = new Date();
  const messageDate = new Date(date);
  
  // If message is from today, show only time
  if (messageDate.toDateString() === now.toDateString()) {
    return messageDate.toLocaleTimeString('en-US', options);
  }
  
  // If message is from this year, show date and time
  options.month = 'short';
  options.day = 'numeric';
  if (messageDate.getFullYear() === now.getFullYear()) {
    return messageDate.toLocaleString('en-US', options);
  }
  
  // If message is from a different year, show full date and time
  options.year = 'numeric';
  return messageDate.toLocaleString('en-US', options);
}

export function ChatMessage({ message }: ChatMessageProps) {
  const isBot = message.role === 'assistant';
  
  marked.setOptions({
    breaks: true,
    gfm: true
  });

  const sanitizeConfig = {
    ALLOWED_TAGS: [
      'p', 'br', 'strong', 'em', 'code', 'pre', 'blockquote',
      'h1', 'h2', 'h3', 'h4', 'h5', 'h6',
      'ul', 'ol', 'li', 'a', 'hr'
    ],
    ALLOWED_ATTR: ['href', 'target', 'rel', 'class']
  };

  const processedContent = message.content
    .split('\n')
    .map(line => line.trim())
    .join('\n');
  const htmlContent = marked(processedContent);
  const sanitizedContent = DOMPurify.sanitize(htmlContent, sanitizeConfig);
  
  return (
    <div className={`flex gap-3 p-3 ${isBot ? 'bg-blue-50' : 'bg-white'}`}>
      <div className="flex-shrink-0">
        {isBot ? (
          <div className="w-7 h-7 rounded-full bg-blue-600 flex items-center justify-center">
            <Bot className="w-4 h-4 text-white" />
          </div>
        ) : (
          <div className="w-7 h-7 rounded-full bg-gray-600 flex items-center justify-center">
            <User className="w-4 h-4 text-white" />
          </div>
        )}
      </div>
      <div className="flex-1 min-w-0 overflow-hidden">
        <div className="flex items-center justify-between mb-0.5">
          <p className="text-sm font-medium text-gray-900">
            {isBot ? 'l3lueGPT' : 'You'}
          </p>
          <span className="text-xs text-gray-500">
            {formatTimestamp(message.timestamp)}
          </span>
        </div>
        <div 
          className="prose prose-sm max-w-none break-words text-gray-900 [&>*:first-child]:mt-0 [&>*:last-child]:mb-0 [&>p]:my-1 [&>ul]:my-1 [&>ol]:my-1 [&>pre]:my-1 [&>blockquote]:my-1 [&>pre]:whitespace-pre-wrap [&>pre]:break-words [&>code]:break-words [&>blockquote]:break-words"
          dangerouslySetInnerHTML={{ __html: sanitizedContent }}
        />
      </div>
    </div>
  );
}