import { Message } from '../types/chat';
import { marked } from 'marked';
import { toast } from 'react-hot-toast';

marked.setOptions({
  gfm: true,
  breaks: true,
  sanitize: false,
  smartLists: true,
  smartypants: true
});

async function checkServerStatus(): Promise<boolean> {
  try {
    const response = await fetch('/api/health');
    return response.ok;
  } catch {
    return false;
  }
}

let serverStartNotified = false;

export async function getAIResponse(messages: Message[]): Promise<string> {
  if (!messages.length) {
    throw new Error('No messages provided');
  }

  // Check server status first
  const isServerRunning = await checkServerStatus();
  if (!isServerRunning) {
    if (!serverStartNotified) {
      toast.error('Please run npm run start:server in a new terminal to start the AI server');
      serverStartNotified = true;
    }
    throw new Error('AI server is not running');
  }

  // Reset notification flag when server is running
  serverStartNotified = false;

  try {
    const response = await fetch('/api/generate', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
      body: JSON.stringify({ 
        messages: messages.map(msg => ({
          role: msg.role,
          content: msg.content
        }))
      })
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    if (!data.response) {
      throw new Error('Invalid response format from server');
    }

    return data.response;
  } catch (error) {
    console.error('AI response error:', error);
    throw new Error(`AI service error: ${error.message}`);
  }
}